*{box-sizing:border-box}body{background:#532373;background:linear-gradient(45deg,#3f1460,#6a2046 100%);-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;color:#000017;text-align:center;font-style:normal}.wrapper{height:100vh;width:100%}.app{max-width:600px;max-height:600px;width:100%;height:100%;margin:20px;border:1px solid rgba(55,60,255,.56)}.hl{color:#ffa61d}.strong{font-weight:700}.flex{display:-webkit-box;display:-webkit-flex;display:-ms-flexbox;display:flex}.flex.center{-webkit-box-align:center;-webkit-align-items:center;-ms-flex-align:center;align-items:center}.col,.row{-webkit-box-orient:horizontal;-webkit-box-direction:normal;-webkit-flex-direction:row;-ms-flex-direction:row;flex-direction:row}.flex.center,.row.center{-webkit-box-pack:center;-webkit-justify-content:center;-ms-flex-pack:center;justify-content:center}.col{-webkit-box-orient:vertical;-webkit-flex-direction:column;-ms-flex-direction:column;flex-direction:column}.col.center{-webkit-align-content:center;-ms-flex-line-pack:center;align-content:center;-webkit-box-align:center;-webkit-align-items:center;-ms-flex-align:center;align-items:center}
